import React from 'react';
import { useEffect } from "react"
import {useDispatch, useSelector} from "react-redux"
import {loadWeather} from "../actions/weatherActoin"
import {ForecastSlot} from "./ForecastSlot"
import styled from "styled-components"
import { FiSun } from "react-icons/fi";
import FadeIn from 'react-fade-in';

export const WeatherDisplayer = () => {
    const CurrentWeather = useSelector((state) => state.currentWeather)
    const Forecast = useSelector((state) => state.weatherForecast)
    const isLoading = useSelector((state) => state.isLoading)

    const dispatch = useDispatch()

    useEffect(()=>{
        dispatch(loadWeather("kyoto"))
    },[dispatch])

    return(
        <section className="main">
            {!isLoading ? (
                <>
                    <section className="current-weather-section section">
                        <h1 className="title">Current weather</h1>
                            <FadeIn>
                                <article>
                                    <h1 className="location-name">{CurrentWeather.data.location.name}</h1>
                                    <h3 className="local-time">{CurrentWeather.data.location.localtime}</h3>
                                    <h2 className="weather-condition">{CurrentWeather.data.current.condition.text}</h2>
                                    <img className="weather-icon" src={CurrentWeather.data.current.condition.icon} alt="" />
                                    <p className="weather-temperature">Temp: {CurrentWeather.data.current.temp_c} C</p>
                                    <p className="wind-speed">Wind: {CurrentWeather.data.current.wind_kph} km/h</p>
                                </article>
                            </FadeIn>
                        </section>

                        <section className="daily-forecast-section section">
                            <FadeIn>
                                <h1 className="title">Forecast for {CurrentWeather.data.location.name}</h1>
                                <section className="forcast-container">
                                    {Forecast.data.forecast.forecastday.map((item)=>{
                                        const {date, date_epoch, day, hour} = item
                                        return <ForecastSlot key={date_epoch} date={date} data={day} weatherByHours={hour}/>
                                    })}
                                </section>
                            </FadeIn>
                        </section>

                        <section className="app-description section">
                            <p>                            Weather data provided by <a href="https://www.weatherapi.com/" rel="noreferrer" target="_blank"> weatherapi.com</a></p>
                        </section>
                    </>  
            ):(<LoadingMessage>
                <FiSun id="iconAnimate" size={150}/>
                    <h1>The weather is loading!</h1>
                    <h4>if the page won't load that means that server isn't responding</h4>
                    <h3>Try to fill your input correctly or reload page.</h3>
                </LoadingMessage>)}
        </section>
    )
}

const LoadingMessage = styled.div`
    color: #0086b3;
    height: 50vh;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`